@import '~@talend/bootstrap-theme/src/theme/guidelines';
.tc-ratio-bar-lines {
	width: 100%;
	height: 100%;
	border-radius: 1px;
}

.tc-ratio-bar-line-quality-empty {
	@extend .tc-ratio-bar-lines;
	background-color: $dove-gray;
}
.tc-ratio-bar-line-quality-valid {
	@extend .tc-ratio-bar-lines;
	background-color: $rio-grande;
}
.tc-ratio-bar-line-quality-invalid {
	@extend .tc-ratio-bar-lines;
	background-color: $jaffa;
}
